class PercentageChart {
  constructor(id) {
    this.duration    = 1500;
    this.transition  = 200;
    this.width       = 170;
    this.height      = 170;
    this.percentage  = $(id).attr("data-percentage");
    this.dataset     = { lower: calcPercent(0),
                          upper: calcPercent(percentage) 
                        };
    
    this.radius      = Math.min(width, height) / 3;
    this.pie         = d3.pie().sort(null);
    this.format      = d3.format(".0%"); 
    this.color       = d3.scaleOrdinal(["#00539C", "#EEEEEE"])
    this.arc         = d3.arc()
                          .innerRadius(radius * .95)
                          .outerRadius(radius);
    this.svg         = d3.select(id).append("svg")
                         .attr("width", width)
                         .attr("height", height)
                         .append("g")
                         .attr("transform", "translate(" + width/2 + "," + height/2 + ")");
    this.path        = svg.selectAll("path")
                          .data(pie(dataset.lower))
                          .enter().append("path")
                          .attr("fill", (d, i) => { return color(i); })
                          .attr("d", arc)
                          .each((d) => {
                            this._current = d;
                          });
    this.text        = svg.append("text")
                          .attr("text-anchor", "middle")
                          .attr("dy", ".3em");
                  
    this.progress = 0;
  }
}
window.percentageChart = function(id) {
  setTimeout(() => {
    this.path = this.path.data(pie(this.dataset.upper));
    this.path.transition().duration(this.duration).attrTween("d", (d) => {
      const dp = d3.interpolate(this._current, d)
      const p = d3.interpolate(this.progress, this.percentage);
      
      return function(t) {
        this.text.text(format(p(t) / 100));
        return arc(dp(t));
      }
    });
  }, this.transition);


  function calcPercent(percentage) {
    return [parseInt(percentage), 100 - parseInt(percentage)]
  }
}
